import React, { Component } from 'react';

class Contact extends Component {
    render() {
  
      
  
      return (
      <section id="contact" className="cont">
        
        <div className="container text-center" >
            <span className="GIT">GET IN TOUCH</span>
            <br/><br/>
            <div className="row">
                <div className="col-sm-8">
      
                    <form>
                    
                    <input className="formcontrol" type="text" id="fname" name="fname" placeholder="Name"/>
                    
                    <input className="formcontrol" type="text" id="lname" name="lname" placeholder="Email"/>
                    
                    <textarea className="textarea" type="text" id="subject" name="subject" placeholder="Comment" rows="5"></textarea>
                    
                    </form>
                </div>
    

                <div className="col-sm-4">
                <div className="contact1">
                <p>Fan? Drop a note.</p>
                <p><span class="glyphicon glyphicon-map-marker"></span>Trichy, Tamilnadu</p>
                <p><span class="glyphicon glyphicon-phone"></span>Phone: +91-9597235056 </p>
                <p><span class="glyphicon glyphicon-envelope"></span>Email: skesavan272@gmail.com</p>
                </div>
                </div>
            </div>
        </div>
        
      </section>
      );
    }
  }
  
  export default Contact;