import React, { Component } from 'react';

class About extends Component {
  render() {

    

    return (
    <section id="about">
      
        <div className="row ROW">
            <div className="col-sm-6">
                <div id="about" className="about ">
                    <br/><br/>
                    <center><h1>WHO WE ARE</h1></center>
                    <p>KAVERI COTTAGE was created in 2012 and is a customer-centric company which supply goods to the markets. We are specialized in making products such as readymade switch boards bade of Teak wood, meter boards in Teak woods, concealed box, wood plucks etc. Since then, our client base has been growing exponentially in numbers. The key principle of sustainable waste management is to try to deal with waste as high up on the waste hierarchy as possible. The best option is to recycle the wastes and indeed, we are proud to say that we have been using the leftover woods from the woodworking factories and upcycling them into new materials or products of better quality and environmental value. At the same time, we have made efforts to provide job opportunity in rural areas because all our workers are from different villages. Our motive is to produce goods which are perfect and enduring! Teamwork and Determination is what that drives us to make new products and satisfy our customers. Get in touch to have a long-term partnership. 
                    </p>
                    <br/>
                </div>
            </div>
            <div className="col-sm-6">
                <div id="about" className="container text-center">
                    <br/><br/><br/>
                    <img className="i" width="632" height="425"/>              
                </div>
            </div>
        </div>
    </section>
    );
  }
}

export default About;