import React, { Component } from 'react';

class Header extends Component {
  render() {


    function myFunction() {
      alert("Kindly contact us through e-mail or moobile for further details.");
    }
        return (
      <header className="bg" id="home">
            <nav className="navbar navbar-fixed-top">
      
                    <div className="container">
                        <div className="navbar-header">               
                            <div  className="myNavbar">
                                
                                <ul id="top-menu" className="nav navbarnav">
                                    <li className="active"><a href="#home" >HOME</a></li>
                                    <li className="active"><a href="#about">ABOUT</a></li>
                                    <li className="active"><a href="#product">PRODUCT</a></li>
                                    <li className="active"><a href="#contact">CONTACT</a></li>
                                    <li className="active"><a href="#pricing" onClick={myFunction}>PRICING</a></li>   
                                </ul>
                                
                            </div>
                        </div>
                    </div>
            </nav>
            <br/><br/><br/><br/><br/>
      
     
    
      
      <div className= "hir text-center">
        Welcome to Kaveri Cottage!
        <h1>Perfect and Enduring</h1>
      </div>
      
      </header>
      
    );
  }
}
  export default Header;